<template>
  <v-container fluid fill-height style="padding:0;" id="symbol" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="Symbol">Symbol</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>Symbol</code>返回唯一值</p>
                    <p class=".body-1">2. <code>Symbol()</code>产生一个局部的symbol，只能在当前代码上下文中使用</p>
                    <p class=".body-1">3. <code>Symbol.for()</code>能够产生全局symbol，可以跨文件使用。<code>Symbol.for</code>会首先在全局的Symbol寄存器中找是否存在指定的Symbol，如果没有就创建一个全局Symbol</p>
                    <p class=".body-1">4. <code>Symbol.keyFor</code>根据传入的Symbol到全局寄存器中查找是否有这个Symbol如果有返回Symbol的key，如果没有返回undefined</p>
                    <p class=".body-1">5. <code>for...in</code>无法遍历Symbol，<code>Object.getOwnPropertyNames</code>获取不到Symbol，<code>JSON</code>对Symbol无视</p>
                    <pre class="code">{{pres[6]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="asyncIterator">异步迭代（ES9）</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. 利用Generator文章中的例子，结合<code>Symbol.asyncIterator</code>实现一个异步迭代</p>
                    <p class=".body-1">2. demo要实现的功能：传递任意多个数组，将每一个数组的最大值和所有数组的最大值拼接成一个数组输出</p>
                    <pre class="code">{{pres[0]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="hasInstance">自定义instanceof运算符（ES6）</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>Symbol.hasInstance</code>可以自定义instanceof操作符</p>
                    <pre class="code">{{pres[1]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="isConcatSpreadable">定义concat拼接方式（ES6）</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>concat</code>方法调用时，会将数组中的元素取出拼成一个数组，通过<code>Symbol.isConcatSpreadable</code>可以设置concat是否取出元素</p>
                    <p class=".body-1">2. 类数组调用concat默认不会取出元素进行合并</p>
                    <pre class="code">{{pres[2]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="iterator">自定义迭代器（ES6）</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. 设置<code>Symbol.iterator</code>可以实现自定义迭代器，要使用<code>for...of</code>遍历</p>
                    <pre class="code">{{pres[3]}}</pre>

                    <p class=".body-1">2. 调用<code>Symbol.iterator</code>可以获取可迭代对象，如下遍历字符串</p>
                    <pre class="code">{{pres[4]}}</pre>

                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="toPrimitive">自定义引用类型转化成值类型（ES6）</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">设置<code>Symbol.toPrimitive</code>可以实现自定义值类型转化</p>
                    <pre class="code">{{pres[5]}}</pre>

                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from "timers";
export default {
  components: { Blog },
  data() {
    return {
      time: "2019-1-5",
      title: "Symbol",
      items: [
        {
          title: "Symbol",
          target: "#Symbol"
        },
        { divider: true },
        {
          title: "异步迭代",
          target: "#asyncIterator"
        },
        { divider: true },
        {
          title: "自定义instanceof运算符",
          target: "#hasInstance"
        },
        { divider: true },
        {
          title: "定义concat拼接方式",
          target: "#isConcatSpreadable"
        },
        { divider: true },
        {
          title: "自定义迭代器",
          target: "#iterator"
        },
        { divider: true },
        {
          title: "自定义引用类型转化成值类型",
          target: "#toPrimitive"
        },
        { divider: true },
      ],
      pres: 
[
`
    function findMax() {
        const params = Array.from(arguments); // 获取所有数组
        const length = params.length;
        let tmpMaxs = [];
        let i = 0;

        const asyncIter = {};
        asyncIter[Symbol.asyncIterator] = async function *() {
            yield '初始化'; // yield会有一个初始化的过程
            while(i < length) {
                const tmp = Math.max.apply(null, params[i]);
                tmpMaxs.push(tmp);
                yield tmp; // 获取每一个数组的最大值
                i++;
            }
            const max = Math.max.apply(null, tmpMaxs); // 获取所有数组的最大值
            tmpMaxs.push(max);
            yield max;
        };

        (async () => {
            for await (const item of asyncIter) {
                console.log(item);
            }
        })();
        return tmpMaxs;
    }
    let max = findMax([10,59,1,2,3], [5,8,9,30,21], [1,20,3,0,3]);
    console.log(max); // 输出 [59, 30, 20, 59]
`,
`
    const obj = {};
    obj[Symbol.hasInstance] = function(instance) {
        return Object.prototype.toString.call(instance) === "[object Object]";
    }

    console.log({} instanceof obj); // true
    console.log([] instanceof obj); // false
    `,
    `
    function fn() {
        let arr = ['concat'].concat(arguments);
        return arr;
    }
    fn(1,2,3); // ["concat", Arguments(3)]

    function fn() {
        arguments[Symbol.isConcatSpreadable] = true;
        let arr = ['concat'].concat(arguments);
        return arr;
    }
    fn(1,2,3); // ["concat", 1, 2, 3]
`,
`
    let obj = {};
    obj[Symbol.iterator] = function* (){
        console.log("执行了");
        yield 1;
        yield 2;
        yield 3;
    }
    for(let item of obj) {
        console.log(item)
    }

    输出
    1
    2
    3
`,
`
    const str = "JAVASCRIPT太牛逼了";
    const iter = str[Symbol.iterator]();
    let tmp = iter.next();
    while(!tmp.done){
        console.log(tmp);
        tmp = iter.next();
    }
`,
`
    const obj = {
        [Symbol.toPrimitive](type) {
            if (type == 'number') {
                return 97;
            }
            if (type == 'string') {
                return 'a';
            }
            return 'A';
        }
    };

    console.log(\`\${obj}\`) // a type为string
    console.log(+obj); // 97 type为number
    console.log(''+obj); // A type为default
`,
`
    const sb = Symbol('a');
    console.log(sb.description); // a
    Symbol('a') === Symbol('a'); // false
    Symbol.for('a') === Symbol.for('a'); // true
    Symbol.keyFor(Symbol.for('a')); // "a"
    Symbol.keyFor(Symbol('a')); // undefined
`

]
    };
  },
  mounted() {

  },
  methods: {
      test() {
          

      }
  }
};
</script>

<style lang="scss">
#symbol {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
</style>

